import { Component, OnInit } from '@angular/core';
import { EmpserviceService } from 'src/app/services/empservice.service';
import { Emp } from 'src/app/models/emp';

@Component({
  selector: 'app-emplist',
  templateUrl: './emplist.component.html',
  styleUrls: ['./emplist.component.css']
})
export class EmplistComponent implements OnInit {
  emp1: Emp[];
  constructor(private httpclientservice: EmpserviceService) { }

  ngOnInit() {
    this.getEmpList();
  }

  getEmpList() {
    return this.httpclientservice.getEmployeeList().subscribe(emp1 => { this.emp1 = <Emp[]>emp1; });
  }
  getdata()
  {
    console.log(this.emp1);
  }

}
